.button {
	width: 100%;
	padding: 14px 0;
	border-radius: 4px;
	background: #1A73E8;
	color: #fff;
	cursor: pointer;
	transition: 100ms linear;
	&.disabled {
		background: #9c9c9c;
		pointer-events: none;
	}
	&:hover {
		background: #4b93f1;
	}
	&:active {
		background: #6ba2ea;
	}
}